import classes from "./Users.module.css";
import React from "react";
import Paginator from "../common/Paginator/Paginator";
import User from "./User/User";


const Users = ({users, totalUsersCount, pageSize, currentPage, currentPortion, onPageChanged, onPortionChanged, onArrow, ...props}) => {
    return (
        <div>
            <Paginator totalItemsCount={totalUsersCount} pageSize={pageSize} currentPage={currentPage} currentPortion={currentPortion}
                       onPageChanged={onPageChanged} onPortionChanged={onPortionChanged} portionSize={10}/>
            <div className={classes.users}>
                {users.map(user => <User user={user} key={user.id} followingInProgress={props.followingInProgress}
                                         follow={props.follow} unfollow={props.unfollow}/>)}
            </div>
            <Paginator totalItemsCount={totalUsersCount} pageSize={pageSize} currentPage={currentPage} currentPortion={currentPortion}
                       onPageChanged={onPageChanged} onPortionChanged={onPortionChanged} portionSize={10}/>
        </div>
    )
}

export default Users