import React from "react";
import classes from "./Paginator.module.css";

const Paginator = ({totalItemsCount, currentPage, currentPortion, pageSize, onPageChanged, onPortionChanged, portionSize}) => {
    let pagesCount = Math.ceil(totalItemsCount / pageSize)
    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize)
    let leftPortionNumber = (currentPortion - 1) * portionSize + 1
    let rightPortionNumber = currentPortion * portionSize


    return (
        <div className={classes.pages}>
            {currentPortion > 1 && <span onClick={() => onPortionChanged(currentPortion - 1)}>&#8637;</span>}
            {pages.filter(page => page >= leftPortionNumber && page <= rightPortionNumber)
                .map(page => {
                    return <span className={currentPage === page && classes.selectedPage}
                                 key={page}
                                 onClick={e => onPageChanged(page)}>
                        {page}
                    </span>
                })
            }
            {portionCount > currentPortion && <span onClick={() => onPortionChanged(currentPortion + 1)}>&#8640;</span>}
        </div>
    )
}


export default Paginator