import {createSelector} from "reselect";

const getUsersSelector = state => state.usersPage.users
export const getUsers = createSelector(getUsersSelector, users => users.filter(user => true))

export const getPageSizeSuper = createSelector(state => state.usersPage.pageSize)
export const getTotalUsersCountSuper = createSelector(state => state.usersPage.totalUsersCount)
export const getCurrentPageSuper = createSelector(state => state.usersPage.currentPage)
export const getCurrentPortionSuper = createSelector(state => state.usersPage.currentPortion)
export const getIsFetchingSuper = createSelector(state => state.usersPage.isFetching)


export const getPageSize = state => {
    return state.usersPage.pageSize
}

export const getTotalUsersCount = state => {
    return state.usersPage.totalUsersCount
}

export const getCurrentPage = state => {
    return state.usersPage.currentPage
}

export const getCurrentPortion = state => {
    return state.usersPage.currentPortion
}

export const getIsFetching = state => {
    return state.usersPage.isFetching
}

export const getFollowingInProgress = state => {
    return state.usersPage.followingInProgress
}