import classes from "./User.module.css";
import React from "react";
import {NavLink} from "react-router-dom";


const User = ({user, follow, unfollow, followingInProgress}) => {
    return (
        <div key={user.id} className={classes.user}>
            <div className={classes.avatar}>
                <NavLink to={`/profile/${user.id}`}>
                    <img src={user.photos.small != null ? user.photos.small :
                        'https://www.enduresc.com.au/wordpress/wp-content/uploads/2018/05/user-profile.jpg'
                    } alt="ll"/>
                </NavLink>
            </div>
            <div className={classes.about}>
                <div className={classes.name}>
                    <NavLink to={`/profile/${user.id}`}>
                        {user.name}
                    </NavLink>
                </div>
                <div className={classes.status}>
                    {user.status}
                </div>
            </div>
            <div className={classes.location}>
                {/*{user.location.city}, {user.location.country}*/}
                {'user.location.city'}, {'user.location.country'}
            </div>
            <div className={classes.button}>
                {user.followed
                    ?
                    <button disabled={followingInProgress.includes(user.id)} className={classes.unfollow}
                            onClick={() => {
                                unfollow(user.id)
                            }}>Unfollow</button>
                    : <button disabled={followingInProgress.includes(user.id)} className={classes.follow}
                              onClick={() => {
                                  follow(user.id)
                              }}>Follow</button>}
            </div>
        </div>
    )
}


export default User