import {connect} from "react-redux";

import {
    setCurrentPage,
    setCurrentPortion,
    requestUsers,
    unfollow,
    follow
} from "../../redux/usersReducer";
import React from "react";
import Users from "./Users";
import Preloader from "../common/preloader/Preloader";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import {
    getUsers,
    getCurrentPage,
    getFollowingInProgress,
    getIsFetching,
    getPageSize,
    getTotalUsersCount, getCurrentPortion
} from "../../redux/usersSelector";


class UsersContainer extends React.Component {
    componentDidMount() {
        this.props.requestUsers(this.props.currentPage, this.props.pageSize)
    }

    onPageChanged = page => {
        this.props.setCurrentPage(page)
        this.props.requestUsers(page, this.props.pageSize)
    }

    onPortionChanged = portion => {
        this.props.setCurrentPortion(portion)
    }


    render() {
        return <>
            {this.props.isFetching ? <Preloader/> :
                <Users totalUsersCount={this.props.totalUsersCount} pageSize={this.props.pageSize}
                       currentPage={this.props.currentPage}
                       currentPortion={this.props.currentPortion}
                       followingInProgress={this.props.followingInProgress}
                       users={this.props.users}
                       follow={this.props.follow} unfollow={this.props.unfollow}
                       onPageChanged={this.onPageChanged} onPortionChanged={this.onPortionChanged}
                       onArrow={this.onArrow}/>}
        </>
    }
}


let mapStateToProps = state => {
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        currentPortion: getCurrentPortion(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}


export default compose(connect(mapStateToProps, {
    follow,
    unfollow,
    setCurrentPage,
    setCurrentPortion,
    requestUsers
}), withAuthRedirect)(UsersContainer)